
html,
body {
  font-weight: 200;
  font-family: 'PingFang SC', 'STHeitiSc-Light', 'Helvetica-Light', Arial, sans-serif;
}
@media (-webkit-min-device-pixel-ratio: 1.5), (min-device-pixel-ratio: 1.5) {
.border-1px:after {
    -webkit-transform: scaleY(0.7);
    transform: scaleY(0.7);
}
}
@media (-webkit-min-device-pixel-ratio: 2), (min-device-pixel-ratio: 2) {
.border-1px:after {
    -webkit-transform: scaleY(0.5);
    transform: scaleY(0.5);
}
}
.clearfix:after {
  content: "";
  height: 0;
  visibility: hidden;
  overflow: hidden;
  display: block;
  clear: both;
}
.border-none:after {
  display: none;
}
@font-face {
  font-family: 'sell-icon';
  src: url('common/fonts/sell-icon.eot?9i1abc');
  src: url('common/fonts/sell-icon.eot?9i1abc#iefix') format('embedded-opentype'), url('common/fonts/sell-icon.ttf?9i1abc') format('truetype'), url('common/fonts/sell-icon.woff?9i1abc') format('woff'), url('common/fonts/sell-icon.svg?9i1abc#sell-icon') format('svg');
  font-weight: normal;
  font-style: normal;
}
[class^="icon-"],
[class*=" icon-"] {
  /* use !important to prevent issues with browser extensions that change fonts */
  font-family: 'sell-icon' !important;
  speak: none;
  font-style: normal;
  font-weight: normal;
  font-variant: normal;
  text-transform: none;
  line-height: 1;
  /* Better Font Rendering =========== */
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
.icon-add_circle:before {
  content: "\e900";
}
.icon-arrow_lift:before {
  content: "\e901";
}
.icon-check_circle:before {
  content: "\e902";
}
.icon-close:before {
  content: "\e903";
}
.icon-favorite:before {
  content: "\e904";
}
.icon-keyboard_arrow_right:before {
  content: "\e905";
}
.icon-remove_circle_outline:before {
  content: "\e906";
}
.icon-shopping_cart:before {
  content: "\e907";
}
.icon-thumb_down:before {
  content: "\e908";
}
.icon-thumb_up:before {
  content: "\e909";
}
#app .tab {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  width: 100%;
  height: 40px;
  line-height: 40px;
  position: relative;
}
#app .tab:after {
  content: "";
  display: block;
  position: absolute;
  left: 0;
  bottom: 0;
  width: 100%;
  border-top: 1px solid rgba(7, 17, 27, 0.1);
}
#app .tab .tab-item {
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
  text-align: center;
}
#app .tab .tab-item > a {
  display: block;
  font-size: 14px;
  color: #4d555d;
}
#app .tab .tab-item > a.active {
  color: #f01414;
}



