.header {
  position: relative;
  color: #fff;
  font-size: 0;
  overflow: hidden;
  background-color: rgba(7, 17, 27, 0.5);
  /*主体区*/
  /*公告区*/
  /*背景图*/
  /*商品详情*/
}
.header .content-wrapper {
  position: relative;
  padding: 24px 12px 18px 24px;
}
.header .content-wrapper .avatar {
  display: inline-block;
  vertical-align: top;
}
.header .content-wrapper .avatar img {
  border-radius: 2px;
}
.header .content-wrapper .content {
  display: inline-block;
  margin-left: 16px;
  font-size: 14px;
}
.header .content-wrapper .content .title {
  margin: 2px 0px 8px;
}
.header .content-wrapper .content .title .brand {
  display: inline-block;
  vertical-align: top;
  width: 30px;
  height: 18px;
  background-image: url("../../components/header/brand@2x.png");
  background-size: 30px 18px;
  background-repeat: no-repeat;
}
@media (-webkit-min-device-pixel-ratio: 3), (min-device-pixel-ratio: 3) {
.header .content-wrapper .content .title .brand {
    background-image: url("../../components/header/brand@3x.png");
}
}
.header .content-wrapper .content .title .name {
  margin-left: 6px;
  font-size: 16px;
  font-weight: bold;
  line-height: 18px;
}
.header .content-wrapper .content .description {
  font-size: 12px;
  line-height: 12px;
  margin-bottom: 10px;
}
.header .content-wrapper .content .support {
  font-size: 0;
}
.header .content-wrapper .content .support .icon {
  display: inline-block;
  width: 12px;
  height: 12px;
  line-height: 12px;
  margin-right: 4px;
  background-size: 12px;
  background-repeat: no-repeat;
  vertical-align: top;
}
.header .content-wrapper .content .support .icon.decrease {
  background-image: url("../../components/header/decrease_1@2x.png");
}
@media (-webkit-min-device-pixel-ratio: 3), (min-device-pixel-ratio: 3) {
.header .content-wrapper .content .support .icon.decrease {
    background-image: url("../../components/header/decrease_1@3x.png");
}
}
.header .content-wrapper .content .support .icon.discount {
  background-image: url("../../components/header/discount_1@2x.png");
}
@media (-webkit-min-device-pixel-ratio: 3), (min-device-pixel-ratio: 3) {
.header .content-wrapper .content .support .icon.discount {
