    background-image: url("../../components/header/bulletin@3x.png");
}
}
.header .bulletin-wrapper .bulletin-text {
  vertical-align: top;
  margin: 0 4px;
  font-size: 10px;
}
.header .bulletin-wrapper .icon-keyboard_arrow_right {
  position: absolute;
  right: 12px;
  top: 9px;
  font-size: 10px;
}
.header .background {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: -1;
  -webkit-filter: blur(10px);
          filter: blur(10px);
}
.header .detail {
  position: fixed;
  z-index: 100;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  overflow: auto;
  -webkit-transition: all .5s;
  transition: all .5s;
  background: rgba(7, 17, 27, 0.8);
  /*关闭商品详情*/
}
.header .detail.fade-enter-active {
  opacity: 1;
}
.header .detail.fade-enter,
.header .detail.fade-leave-active {
  opacity: 0;
}
.header .detail .detail-wrapper {
  min-height: 100%;
}
.header .detail .detail-wrapper .detail-main {
  padding-top: 64px;
  padding-bottom: 64px;
}
.header .detail .detail-wrapper .detail-main .name {
  text-align: center;
