  margin-top: 16px;
}
.header .detail .detail-wrapper .title {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  width: 80%;
  margin: 28px auto 24px;
  font-size: 14px;
}
.header .detail .detail-wrapper .title .line {
  position: relative;
  top: 7px;
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
  height: 2px;
  background-color: rgba(255, 255, 255, 0.2);
}
.header .detail .detail-wrapper .title .text {
  padding: 0 12px;
  font-weight: 700;
  font-size: 14px;
}
.header .detail .detail-wrapper .supprts {
