}
.header .detail .detail-wrapper .supprts .support-item .text {
  font-size: 12px;
  line-height: 16px;
}
.header .detail .detail-wrapper .bulletin-text {
  width: 80%;
  margin: 0 auto;
  padding: 0 12px;
  font-size: 12px;
  font-weight: 200;
  line-height: 24px;
}
.header .detail .detail-close {
  position: relative;
  width: 32px;
  height: 32px;
  margin: -64px auto 0;
  clear: both;
  font-size: 32px;
  color: rgba(255, 255, 255, 0.5);
}



